<script>

import axios from "axios";
import Datepicker from 'vuejs-datepicker';
import { de } from 'vuejs-datepicker/dist/locale';

export default {
  name: "ContactForm1",
  components: {
    Datepicker
  },
  data() {
    return {
      de: de,
      errors: 0,
      name: "",
      emailAddress: "",
      phoneNumber: "",
      weddingDate: "",
      weddingLocation: "",
      desiredService: "",
      weddingStyle: "",
      message: "",
      notice: "",
      terms: ""
    }
  },
  methods: {
    checkForm: function() {
      let errorFields = document.querySelectorAll(".cErrors");
      let counter = 0;
      this.errors = 0;

      errorFields.forEach(errorField => {
        errorField.style.display = "none";
      });

      if (this.name === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.emailAddress === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.phoneNumber === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.weddingDate === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.weddingLocation === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.desiredService === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.weddingStyle === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.message === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.notice === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
      counter++;
      if (this.terms === "") {
        this.errors += 1;
        errorFields[counter].style.display = "flex";
      }
    },
    sendMail: function() {
      //console.log(
      //    this.name,
      //    this.emailAddress,
      //    this.phoneNumber,
      //    this.weddingDate,
      //    this.weddingLocation,
      //    this.desiredService,
      //    this.weddingStyle,
      //    this.message,
      //    this.notice,
      //    this.terms
      //)
      this.checkForm()
      if (this.errors == 0) {
        axios.post("http://127.0.0.1:5000/send", {
          firstName: this.firstName,
          lastName: this.lastName,
          emailAddress: this.emailAddress,
          phoneNumber: this.phoneNumber,
          weddingDate: this.weddingDate,
          weddingLocation: this.weddingLocation,
          weddingStyle: this.weddingStyle,
          desiredService: this.desiredService,
          message: this.message,
          notice: this.notice
        })
        .then(function (response) {
          alert(response.data);
          console.log(response);
        })
        .catch(function (error) {
          alert("ERROR!");
          console.log(error);
        });
      }

    }
  },
}

</script>

